.scrollTop {
    position: fixed; 
    width: 100%;
    bottom: 20px;
    -webkit-align-items: center;
            align-items: center;
    height: 3rem;
    -webkit-justify-content: center;
            justify-content: center;
    z-index: 1000;
    cursor: pointer;
    -webkit-animation: fadeIn 0.3s;
            animation: fadeIn 0.3s;
    transition: opacity 0.4s;
    opacity: 0.5;
    color: #7952B3;
  }
  
  .scrollTop:hover{
    opacity: 1;
  }
  
  @-webkit-keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }
  
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }

  
