.nav-menu{
        width: auto;
        height: 100vh;
        background-color: #1E2A3A;
        display: none;
        position: fixed;
        top: 0;
        right: 0;
        z-index: 2;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    
    .nav-menu.active{
